/* --- Main Container --- */
.main-container {
  background-color: var(--background-color);
  font-family: 'JetBrains Mono', monospace;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
  box-sizing: border-box;
}

/* --- Terminal Window --- */
.terminal-window {
  width: 100%;
  max-width: 800px;
  height: 600px;
  background-color: var(--card-background);
  border: 1px solid var(--card-border);
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.terminal-header {
  background: #d1d1d6; 
  padding: 8px 12px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--card-border);
}

[data-theme='dark'] .terminal-header {
  background: #333333; 
}

.terminal-buttons { display: flex; gap: 8px; }
.dot { width: 12px; height: 12px; border-radius: 50%; }
.dot.red { background: #ff5f56; }
.dot.yellow { background: #ffbd2e; }
.dot.green { background: #27c93f; }

.terminal-title {
  color: var(--text-secondary);
  font-size: 0.8rem;
  margin: 0 auto;
}

/* --- Terminal Body & Text --- */
.terminal-body {
  flex-grow: 1;
  padding: 1rem;
  overflow-y: auto;
  font-size: 1rem;
  line-height: 1.6;
  text-align: left; /* This is the fix */
}

.command-line, .input-form {
  display: flex;
  white-space: pre-wrap;
  word-break: break-all;
}

.line-text {
  color: var(--text-secondary);
  white-space: pre-wrap;
  word-break: break-all;
}

.command-line .line-text {
  color: var(--text-primary);
}

.prompt-symbol {
  color: var(--text-primary);
  margin-right: 0.5rem;
  font-weight: 700;
}

/* --- Input & Cursor --- */
.input-form {
  width: 100%;
}

.terminal-input {
  flex-grow: 1;
  background: transparent;
  border: none;
  outline: none;
  color: var(--text-primary);
  font-family: 'JetBrains Mono', monospace;
  font-size: 1rem;
  caret-color: var(--text-primary);
}

/* --- Help Command List Styling --- */
.output-list {
  color: var(--text-secondary);
}

.output-list p { margin: 0; }

.output-list ul {
  list-style-type: none;
  padding-left: 1rem;
  margin-top: 0.5rem;
}

.output-list li { display: flex; align-items: center; }
.output-list li::before { content: "-"; margin-right: 0.75rem; }

.command-name {
  color: var(--text-primary);
  font-weight: 500;
  width: 100px;
  flex-shrink: 0;
}

.command-name::after { content: ":"; margin-left: 0.5rem; }
.command-description { color: var(--text-secondary); }

/* --- Mobile Responsiveness --- */
@media (max-width: 768px) {
  .main-container { padding: 1rem; align-items: flex-start; }
  .terminal-window { height: calc(100vh - 100px); }
  .terminal-body { font-size: 0.9rem; }
}